<template>
  <el-form label-width="120px" :model="realForm" :rules="rules" :inline="true" ref="realForm">
    <el-form-item label="机构" prop="org">
      <el-select v-model="realForm.org" placeholder="请选择机构" clearable>
        <el-option v-for="item in realFormData.orgList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="渠道" prop="channel">
      <el-select v-model="realForm.channel" placeholder="请选择渠道" clearable>
        <el-option v-for="item in realFormData.channelList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="统计粒度" prop="size">
      <el-select v-model="realForm.size" placeholder="请选择统计粒度" clearable>
        <el-option v-for="item in realFormData.sizeList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="统计期间" prop="timeRange">
      <el-select v-model="realForm.timeRange" placeholder="请选择统计期间" clearable>
        <el-option v-for="item in realFormData.timeRangeList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="产品类型" prop="productType">
      <el-select v-model="realForm.productType" placeholder="请选择产品类型" clearable>
        <el-option v-for="item in realFormData.productTypeList" :key="item.code" :value="item.code" :label="item.name"></el-option>
      </el-select>
    </el-form-item>
  </el-form>
</template>

<script>
import {mapState} from 'vuex'
export default {
  name: 'RealQueryForm',
  props: {
    realForm: {
      type: Object,
      required: true
    },
    rules: {
      type: Object,
      default: null
    }
  },
  created () {
    this.$store.dispatch('initRealFormData')
  },
  computed: {
    ...mapState({
      realFormData: state => state.inspect.realFormData
    }),
    inspectType () {
      return this.inspect.type
    }
  }
}
</script>

<style scoped>

</style>



